<template>
  <Loader v-if="loading" />
  <router-view v-else-if="place" />
  <ErrorPlace v-else />
</template>

<script>
import Loader from "@/components/Loader";

import { mapState } from "vuex";

import ErrorPlace from "@/components/ErrorPlace";
export default {
  name: "Main",
  components: { Loader, ErrorPlace },

  computed: {
    ...mapState(["loading", "place", "error"]),
  },
  methods: {},
  watch: {
    error(data) {
      console.log("w error", data);
      //https://maronato.github.io/vue-toastification/?ref=madewithvuejs.com
      this.$toast.error(data.code);
    },
  },

  async mounted() {
    const place = this.$route.params.place;
    const table = this.$route.query.table;
    if (place) {
      await this.$store.dispatch("autoGetPlace", place);
      await this.$store.dispatch("autoGetMenuItems", place);
      await this.$store.dispatch("autoGetMenuGroups", place);
      await this.$store.dispatch("autoGetOrders", place);
      await this.$store.dispatch("autoGetWaiters", place);
      this.$store.commit("setTable", table);
    }
  },
};
</script>

